.wrapper,
.wrapper-form {
  width: 100%;
  min-width: @mobile-width;
  margin: 0 auto;
  padding: 0 20px;

  @media (min-width: @tablet-width) {
    min-width: @tablet-width;
    padding: 0 30px;
  }

  @media (min-width: @desktop-width) {
    max-width: @desktop-width;
    padding: 0 110px;
  }
}

.wrapper-form {
  @media (min-width: @tablet-width) {
    min-width: @tablet-width;
    padding: 0 67px 0 70px;
  }

  @media (min-width: @desktop-width) {
    max-width: @desktop-width;
    padding: 0 110px;
  }
}
